.hero .container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 4rem auto;
    padding: 1rem;
    align-items: center;
    justify-content: center;
}

.hero .left, .hero .right{
    padding: 1rem;
}

.hero h1{
    padding: 1rem 0;
}

.hero .input-container{
    padding: 1rem 0;
}

.hero .right .img-container{
    width: 100%;
    display: flex;
}

.hero img{
    max-width: 80%;
    margin: auto;
}

@media screen and (max-width:940px) {
    .hero .container{
        grid-template-columns: 1fr;
        margin: 1rem 0;
    }
}